<template>
	<view class="container">
		<view class="register">
			<view class="info_box">
				<view class="info_item">
					<text class="label">当前状态</text>
					<text>逾期{{dataInfo.timeOverdue}}天</text>
				</view>
				<view class="info_item">
					<text class="label required">延期天数</text>
					<input v-model="form.delayDay" type="text" placeholder="请输入延期天数" @blur="getDelay" />
				</view>
				<view class="info_item">
					<text class="label">当前失效时间</text>
					<text>{{form.expiredTime}}</text>
				</view>
				<view class="info_item">
					<text class="label">延期后失效时间</text>
					<text>{{form.expiredTime2}}</text>
				</view>
				<remarkBox :textName="'申请原因'" @getValue="_getValue"></remarkBox>
				<view class="info_item">
					<text class="label required">审核人</text>
					<view class="flexBet">
						<ld-select :underlist="uesrList" list-key="label" value-key="value" placeholder="请选择审核人"
							v-model="form.userId" @change="selectUser"></ld-select>
						<uni-icons type="arrowright" size="12" color="#999"></uni-icons>
					</view>
				</view>
			</view>
		</view>
		<view class="footer" @click="sureApply">申请</view>
	</view>
</template>

<script>
	import ldSelect from '@/components/ld-select/ld-select.vue';
	import remarkBox from '@/components/remark.vue';
	import httpUtils from '@/utils/httpUtils.js';
	import Tools from '@/utils/tools.js';
	export default {
		data() {
			return {
				form: {
					code: '', // 延期类型 3延期认购 4延期签约
					id: '',
					delayDay: '', // 延期天数
					expiredTime: '', // 当前失效时间
					expiredTime2: '', // 延期后失效时间
					userId: '', // 审核人id
					applyReason: '', // 申请原因
				},
				unitList: [], // 审核单位
				unitIndex: [], // 审核单位索引
				uesrList: [], // 审核人
				uesrIndex: '', // 审核人索引
				dataInfo: {},
				delayList: [{
					value: '0',
					label: '普通延期'
				}, {
					value: '1',
					label: '超长延期'
				}]
			}
		},
		onLoad(options) {
			this.dataInfo = JSON.parse(decodeURIComponent(options.data))
			this.form.id = this.dataInfo.id
			this.form.expiredTime = this.dataInfo.expiredDate
			this.form.code = options.pageIndex == 1 ? 4 : 3
			this._getUser()
		},
		methods: {
			// 获取审核单位
			// _getUnit(){
			// 	let that = this
			// 	Tools.showLoading('加载中')
			// 	httpUtils.sendHttpRequest('2035', Object.assign({}, ''), function(res) {
			// 		Tools.hideLoading()
			// 		if (res.data.code == '0') {
			// 			that.unitList = res.data.data
			// 		}
			// 	})
			// },
			// 获取审核人
			_getUser() {
				let that = this
				Tools.showLoading('加载中')
				httpUtils.sendHttpRequest('2036', Object.assign({}, ''), function(res) {
					Tools.hideLoading()
					if (res.data.code == '0') {
						that.uesrList = res.data.data.map(item => {
							return {
								value: String(item.id),
								label: String(item.username),
							}
						})
					}
				})
			},
			// 获取失效时间
			getDelay() {
				if (!this.form.delayDay) {
					Tools.showErrorToast('请填写延期天数')
					return
				}
				let that = this,
					postData = {
						expiredTime: that.form.expiredTime,
						delayDay: that.form.delayDay
					}
				Tools.showLoading('加载中')
				httpUtils.sendHttpRequest('2037', Object.assign({}, postData), function(res) {
					Tools.hideLoading()
					if (res.data.code == '0') {
						that.form.expiredTime2 = res.data.data
					}
				})
			},
			// 审核单位选择
			// selectUnit(e, val) {
			// 	this.unitIndex = e.target.value
			// 	this._getUser(val[this.unitIndex].value)
			// },
			// 审核人选择
			// selectUser(e, val) {
			// 	this.uesrIndex = e.target.value
			// 	this.form.userId = val[this.uesrIndex].id
			// },
			selectUser(val) {
				this.form.userId = val
			},
			// 申请原因回调
			_getValue(data) {
				this.form.applyReason = data
			},
			// 申请
			sureApply() {
				if (!this.form.delayDay) {
					Tools.showErrorToast('请输入延期天数')
					return
				}
				if (!this.form.userId) {
					Tools.showErrorToast('请选择审核人')
					return
				}
				let that = this
				Tools.showLoading('申请中')
				httpUtils.sendHttpRequest('2038', Object.assign({}, that.form), function(res) {
					Tools.hideLoading()
					if (res.data.code == '0') {
						Tools.showSuccessToast('申请成功')
						setTimeout(function() {
							let pages = getCurrentPages(); // 当前页面
							let beforePage = pages[pages.length - 2]; // 上一页
							beforePage.$vm.index = that.form.code == 4 ? 1 : 0;
							uni.navigateBack({
								success: function() {
									beforePage.onLoad(); // 执行上一页的onLoad方法
								}
							});
						}, 500)
					}
				})
			},
			// 取消
			// cancel() {
			// 	uni.navigateBack({
			// 		delta: 1
			// 	})
			// }
		},
		computed: {},
		components: {
			ldSelect, remarkBox
		}
	}
</script>

<style lang="scss">
	@import '@/styles/form.scss';
	.register{
		.info_box{
			.info_item {
				.label{
					min-width: 200rpx;
					width: 200rpx;
				}
			}
		}
	} 
</style>
